import {Component, ElementRef, Renderer2, ViewChild} from '@angular/core';
import {AlertController, IonicPage, LoadingController, NavController, Platform, Searchbar, ViewController} from 'ionic-angular';
import {PAGES2} from '../../../app/global.setting';
import {Geolocation} from '@ionic-native/geolocation';
import {UserService} from "../../../providers/user.service";
import {ArticleService} from "../../../providers/article.service";
import {Article} from "../../../providers/model/article";
import {GoogleMap, GoogleMapOptions, GoogleMapsEvent, HtmlInfoWindow} from "@ionic-native/google-maps";
import {MapServiceProvider} from "../../../providers/map.service";

@IonicPage()
@Component({
  selector: 'page-homemarket',
  templateUrl: 'homeMarket.html'
})
export class HomeMarketPage {
  @ViewChild('map') mapElement: ElementRef;
  @ViewChild('searchbar') searchbar:Searchbar;
  pages:any[];

  map: GoogleMap;

  title: string = 'NeoPlace';
  zoom: number;

  // initial center position for the map
  lat: number;
  lng: number;

  valueSearch: string;
  valueSearchBar: string;

  isDisplaySearch: boolean;
  onFocusSearch: boolean;

  markers = [];
  alive: boolean = true;

  constructor(
    private mapServiceProvider: MapServiceProvider,
    private renderer: Renderer2,
    public loadingCtrl: LoadingController,
    private userService: UserService,
    private articleService: ArticleService,
    public platform: Platform,
    public geolocation: Geolocation,
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    public viewCtrl: ViewController) {

    //Main Menu
    this.pages = PAGES2;
  }

  ionViewWillEnter() {
    this.platform.ready().then(() => {
      if (this.platform.is('android')) {
        if(!this.mapServiceProvider.map) {
          setTimeout(this.loadMapNative.bind(this), 1000);
        } else {
          this.mapServiceProvider.map.setDiv(this.mapElement.nativeElement);
          this.mapServiceProvider.hideServices();
          this.mapServiceProvider.displayArticles();
          if (this.valueSearch) {
            this.valueSearchBar = this.valueSearch;
            this.mapServiceProvider.findArticle(this.valueSearch);
          }
          this.mapServiceProvider.displayFiats();
        }
      } else {
        this.zoom = 8;
        // initial center position for the map
        this.lat = 0;
        this.lng = 0;

        this.loadMap();
        this.loadArticle();
        //this.loadFiatPosition();
      }
    }).catch(() => {
      //console.log("Platform error");
    });
  }


  displaySearchBar() {
    this.isDisplaySearch = !this.isDisplaySearch;
    if(this.isDisplaySearch) {
      setTimeout(() => {
        this.searchbar.setFocus();
      });
    }
  }

  ionViewWillLeave() {
    this.alive = false;
    this.mapServiceProvider.findArticle('');
    this.isDisplaySearch = false;
    // if (this.platform.is('cordova')) {
    //   //this.markers.forEach(marker => marker.remove());
    //   this.mapServiceProvider.setDiv(null, null);
    // }
  }

  loadMapNative() {
    let loader = this.loadingCtrl.create({content: 'Please wait...', duration: 5000});
    loader.present();
    this.geolocation.getCurrentPosition({ maximumAge: 300000, timeout: 5000, enableHighAccuracy: true })
      .then((position) => {


        let mapOptions: GoogleMapOptions = {
          camera: {
            target: {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            },
            zoom: 5,
            tilt: 30
          },
          controls: {
            myLocationButton: true,
            myLocation: true
          }
        };

        this.mapServiceProvider.setDiv(this.mapElement.nativeElement, mapOptions).then(() => {
          //add markers, polyline, etc.
          loader.dismiss().catch();
          // Now you can use all methods safely.
          this.loadArticleNative();
          //this.loadFiatPositionNative();
        });

      }).catch(error => {
        let mapOptions: GoogleMapOptions = {
          camera: {
            target: {
              lat: 0,
              lng: 0
            }
          }
        };

        this.mapServiceProvider.setDiv(this.mapElement.nativeElement, mapOptions).then(() => {
          //add markers, polyline, etc.
          loader.dismiss().catch();
          // Now you can use all methods safely.
          this.loadArticleNative();
          //this.loadFiatPositionNative();
        });
    });
  }

  showList(pages) {
    if(pages.page == "TabsPage") {
      this.navCtrl.setRoot(pages.page);
    } else {
      this.navCtrl.push(pages.page);
    }
  }

  showPage(page) {
    this.navCtrl
      .push(page)
      // .then(() => {
      //   const index = this.viewCtrl.index;
      //   this.navCtrl.remove(index);
      // });
  }

  loadArticleNative() {
    this.articleService.getArticles().takeWhile(() => this.alive).subscribe(articles => {
      articles.forEach(article => {{
        if(article.uid != this.userService._userFirebase.uid) {
          this.mapServiceProvider.map.addMarker({
            icon: 'blue',
            animation: 'DROP',
            position: {
              lat: article.latitude,
              lng: article.longitude
            }
          })
            .then(marker => {
              this.mapServiceProvider.markersArticle.push({article: article, marker: marker});

              let listenRender;

              marker.on(GoogleMapsEvent.MARKER_CLICK)
                .subscribe(() => {
                  var htmlInfoWindow = new HtmlInfoWindow();
                  let html = [
                    "<div class=\"pin\" style=\"width:150px;\"" +  "id=\"" + article.$key + "\">",
                    "<ion-card>",
                    "<div class=\"pin\" style=\"height:113px\">",
                    "<img src=" + article.taked_picture + ">",
                    "</div>",
                    "</ion-card>",
                    "<div class=\"post-description\">",
                    article.price + " " + article.currency + "<br><strong>" + article.title + "</strong> <br><a>See item</a>",
                    "</div>",
                    "</div>"
                  ].join("");

                  htmlInfoWindow.setContent(html);

                  htmlInfoWindow.open(marker);

                  listenRender = this.renderer.listen(this.mapElement.nativeElement, 'click', (evt) => {
                    this.seeDetails(article);
                  });
                });

              marker.on(GoogleMapsEvent.INFO_CLOSE)
                .subscribe(() => {
                  listenRender();
                });

            });
        }
      }
      })
    });
  }

  loadFiatPositionNative() {
    this.userService.getPositions().takeWhile(() => this.alive).subscribe(positions => {
      positions.forEach(position => {
        if(position.$key != this.userService._userFirebase.uid) {
          this.userService.getProfil(position.$key).takeWhile(() => this.alive).subscribe(profil => {
            this.mapServiceProvider.map.addMarker({
              icon: 'green',
              animation: 'DROP',
              position: {
                lat: position.lat,
                lng: position.lng
              }
            })
              .then(marker => {
                this.mapServiceProvider.markersFiat.push(marker);

                let listenRender;

                marker.on(GoogleMapsEvent.MARKER_CLICK)
                  .subscribe(() => {
                    var htmlInfoWindow = new HtmlInfoWindow();
                    let html = [
                      "<div class=\"pin\" style=\"width:150px;\"" + ">",
                      "<ion-avatar item-left>",
                      "<div class=\"pin\" style=\"height:113px\">",
                      "<img src=" + profil.imageUrl + ">",
                      "<strong>" + profil.pseudo + "</strong>",
                      "</div>",
                      "</ion-avatar>",
                      "<div class=\"post-description\">",
                      "Available for fiat exchange with <strong>" + profil.paymentMethod + "</strong> <br><a>Contact the NeoChanger</a>",
                      "</div>",
                      "</div>"
                    ].join("");

                    htmlInfoWindow.setContent(html);

                    htmlInfoWindow.open(marker);

                    listenRender = this.renderer.listen(this.mapElement.nativeElement, 'click', (evt) => {
                      this.contact(profil, position.$key);
                    });
                  });

                marker.on(GoogleMapsEvent.INFO_CLOSE)
                  .subscribe(() => {
                    listenRender();
                  });

              });
          });
        }

      });
    })
  }

  loadArticle() {
    this.articleService.getArticles().takeWhile(() => this.alive).subscribe(articles => {
      articles.forEach(article => {{
          if(article.uid != this.userService._userFirebase.uid) {
            this.addMarkerArticle(article);
          }
        }
      })
    });
  }

  findArticle(text1: string) {
    this.markers.forEach(marker => {
      if(text1 && text1.trim() != '' && marker.article) {
        let text = text1.toLowerCase();

        if(marker.article.title.toLowerCase().indexOf(text) >= 0) {
          marker.visible = true;
        } else if(marker.article.type.toLowerCase().indexOf(text) >= 0) {
          marker.visible = true;
        } else if(marker.article.description.toLowerCase().indexOf(text) >= 0) {
          marker.visible = true;
        } else if(marker.article.brand.toLowerCase().indexOf(text) >= 0) {
          marker.visible = true;
        } else {
          marker.visible = false;
        }
      } else {
        marker.visible = true;
      }
    });
  }

  loadFiatPosition() {
    this.userService.getPositions().takeWhile(() => this.alive).subscribe(positions => {
      positions.forEach(position => {
        this.userService.getProfil(position.$key).takeWhile(() => this.alive).subscribe(profil => {
          this.addPositionFiat(position.lat, position.lng, profil, position.$key)
        });
      });
    })
  }


  seeDetails(item) {
    this.navCtrl.push('ArticlePage',
      {article: item, key: item.$key});
  }

  seeDetailsService(item) {
    this.navCtrl.push('ServicePage',
      {service: item, key: item.$key});
  }


  loadMap(){
    let loader = this.loadingCtrl.create({content: 'Please wait...', duration: 10000});
    loader.present();
    this.geolocation.getCurrentPosition()
      .then((position) => {
        loader.dismiss().catch();

        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;

        this.markers.push({
          lat: this.lat,
          lng: this.lng,
          draggable: false,
          visible: true,
          markerClickable: false,
          iconUrl: "assets/img/position.png"
        });

      }).catch((error) => {
      //console.log('Error : ', error);
      const alert = this.alertCtrl.create({
        title: 'Error',
        subTitle: 'Error while getting your position, try again',
        buttons: ['Close']
      });
      alert.present();
    });
  }

  addMarkerArticle(article: Article){
    this.markers.push({
      lat: article.latitude,
      lng: article.longitude,
      draggable: false,
      visible: true,
      article: article
    });
  }

  addPositionFiat(lat, lng, profil, uid) {
    this.markers.push({
      lat: lat,
      lng: lng,
      draggable: false,
      position: {profil: profil, uid: uid}
    })
  }

  contact(profil, uid) {
    let uidArray = [this.userService._userFirebase.uid, uid];
    uidArray = uidArray.sort();
    this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: uid});
  }

  getItems(ev) {
    // set val to the value of the ev target
    var val = ev.target.value;
    this.valueSearch = val;

    if (this.platform.is('cordova')) {
      this.mapServiceProvider.findArticle(val);
    } else {
      this.findArticle(val);
    }
  }
}



